            h3 1.1.1. Atributos

            p Para Kotler (2017), el desarrollo de un producto o servicio implica definir los beneficios que ofrecer. Estos beneficios se comunican y entregan a travs de atributos del producto como calidad, caractersticas y estilo y diseo.

