            h3 1.1.2. Ciclo de vida

            p Se considera que los productos, al igual que los seres humanos, tienen un ciclo de vida. El concepto ciclo de vida del producto describe las etapas por las que pasa un nuevo producto en el mercado: introduccin, crecimiento, madurez y decadencia (Kerin,2018).

            p Segn Philip Kotler (2017) Despus de lanzar un nuevo producto, la direccin desea una existencia larga y feliz. Aun cuando nadie espera que un producto se venda por siempre, la compaa busca obtener utilidades atractivas que compensen todos los esfuerzos realizados y los riesgos asumidos para lanzarlo. La direccin est consciente de que cada producto tiene un ciclo de vida, aunque no se conozcan de antemano su forma o su duracin exactas.

            img.mt-5.img-a(src="@/assets/curso/t1-i9.svg").img-fluid

            p El ciclo de vida del producto define el curso que siguen las ventas y utilidades de un producto durante su existencia, tiene cinco etapas bien definidas: 

            .row.justify-content-center.mt-5.my-5
                .col-10.col-md-9

                    p.header-step
                        span.header-step-number.text-color-orange 01
                        span.header-step-title
                            | Desarrollo del producto: inicia cuando la compaa encuentra y desarrolla la idea para un nuevo producto o servicio. Durante el desarrollo del producto, las ventas son iguales a cero y los costos de inversin de la compaa se incrementan. (Kotler,2017)

                    p.header-step
                        span.header-step-number.text-color-orange 02
                        span.header-step-title
                            | Introduccin: ocurre cuando el producto se presenta por primera vez en el mercado meta deseado. (Kerin,2018) Es un periodo de crecimiento lento de las ventas conforme el producto o servicio se lanza al mercado. Las utilidades son nulas en esta fase a causa de los considerables gastos del lanzamiento del producto. (Kotler,2017) #[br]Un ejemplo para la etapa de introduccin es el ltimo Smartphone sacado al mercado.

                    p.header-step
                        span.header-step-number.text-color-orange 03
                        span.header-step-title
                            | Crecimiento: se caracteriza por rpidos aumentos en las ventas. En esta fase aparecen los competidores. (Kerin,2018) Es una etapa de aceptacin rpida en el mercado y de incremento en las utilidades. (Kotler,2017)

                    p.header-step
                        span.header-step-number.text-color-orange 04
                        span.header-step-title
                            | Madurez: disminuye el crecimiento de las ventas porque el producto o servicio ya gan la aceptacin de la mayora de los compradores potenciales. El nivel de utilidades se estabiliza o incluso disminuye a causa de los crecientes gastos de marketing para defender el producto frente a la competencia. (Kotler,2017)


                    p.header-step
                        span.header-step-number.text-color-orange 05
                        span.header-step-title
                            | Decadencia: es el periodo en el que tanto las ventas como las utilidades disminuyen. (Kotler,2017) Con frecuencia, un producto entra en esta etapa no por una estrategia equivocada por parte de la empresa, sino por cambios en el entorno. Una empresa seguir una de dos estrategias para manejar un producto en decadencia: la eliminacin o su drstica desaparicin de la lnea de productos y la cosecha que consiste en que el producto sigue ofrecindose, aunque los vendedores no asignan tiempo para venderlo ni se realizan gastos de publicidad. (Kerin,2018)

            p A continuacin, Hernndez (2017) realiza un cuadro sobre las caractersticas e implicaciones de cada etapa del ciclo de vida del producto.

