            h2 1.2. Servicios

            .row.justify-content-center.align-items-center
                
                .col
                    p Un servicio es una forma de producto que consiste en actividades, beneficios o satisfacciones que se ofrecen en venta y siendo esencialmente intangibles, no tienen como resultado la propiedad de algo. Algunos ejemplos son los servicios bancarios, el hospedaje en hoteles, los vuelos en aerolneas, el comercio minorista y la comunicacin inalmbrica (Kotler,2017).
 
                    p Cmo se disea un concepto de servicio? Para Lovelock (2015) El mercadeo de servicios experimentados reconocen la necesidad de adoptar una perspectiva integral del desempeo que desean que sus clientes experimenten. La propuesta de valor debe considerar e integrar tres componentes:


                .col-auto
                    img(src="@/assets/curso/img_11.png", alt="").img-icon

            .row.justify-content-center
                
                .col-lg-10
                    
                    .tarjeta-avatar-b.mb-5.align-items-center
                        img(src='@/assets/curso/img_12.png' alt='AvatarTop')
                        .tarjeta.tarjeta--gris
                            .p-4
                                .h4 Producto bsico:
                                p por lo general, los servicios se definen con respecto a una industria en particular por ejemplo, del cuidado de la salud o del transporte a partir del conjunto bsico de beneficios y soluciones que se entregan a los clientes. As pues, el transporte resuelve la necesidad de trasladar a un individuo o un objeto fsico de un lugar a otro. Ej. Servicio de Hotelera (Lovelock,2015)

                    .tarjeta-avatar-b.mb-5.align-items-center
                        img(src='@/assets/curso/img_13.png' alt='AvatarTop')
                        .tarjeta.tarjeta--gris
                            .p-4
                                .h4 Servicios complementarios:
                                p la entrega del producto bsico suele ir acompaada de una variedad de otras actividades relacionadas con el servicio, a las que denominamos servicios complementarios, los cuales facilitan el uso del producto bsico y agregan valor y atractivo a la experiencia general del cliente. Cuando se agreguen elementos complementarios o se incremente su nivel de desempeo, esto debe hacerse de forma que aumenten el valor percibido del producto bsico y que permitan al proveedor del servicio cobrar un precio ms alto. Ej. En el Servicio de Hotelera los complementarios de servicio de habitacin, restaurante, estacionamiento, zonas hmedas, entre otros. (Lovelock,2015)

                    .tarjeta-avatar-b.mb-5.align-items-center
                        img(src='@/assets/curso/img_14.png' alt='AvatarTop')
                        .tarjeta.tarjeta--gris
                            .p-4
                                .h4 Procesos de entrega:
                                p se refiere a los procesos que se utilizan para entregar tanto el producto bsico como cada uno de los servicios complementarios. El diseo de la oferta de servicios debe considerar los siguientes aspectos: La manera en la que los distintos componentes del servicio se entregan al cliente, la naturaleza del papel del cliente en dichos procesos, la duracin de la entrega y el nivel y estilo prescritos de los servicios que se ofertaran. Ej. En el servicio de Hotelera los Horarios o rutas para el uso de sus servicios. (Lovelock,2015)


        .sub-ecored.mt-5#tema1_2_1
            h3 1.2.1. Experiencias

            p Para diferenciar sus ofertas, ms all del simple hecho de fabricar productos y prestar servicios, las empresas estn creando y entregando experiencias para los clientes con sus marcas o su compaa. Las experiencias siempre han formado parte importante del marketing. Desde hace mucho tiempo Disney crea sueos y recuerdos a travs de sus pelculas y parques temticos. Y hace mucho que Nike declar: No se trata tanto de los zapatos, sino de a dnde te llevan. Hoy todo tipo de empresas estn rehaciendo sus tradicionales bienes y servicios para crear experiencias (Kotler,2017).

            p Los consumidores valoran las experiencias y las soluciones deseadas, y estn dispuestos a pagar por ellas. No se puede poseer seres humanos, aunque s puede contratar (alquilar) su trabajo y experiencia. Al pagar por el uso temporal de un objeto o por el acceso a una instalacin fsica, los clientes disfrutan del uso de cosas que no pueden comprar o justificar su compra, o bien, que prefieren no conservar ni almacenar despus de su uso. Adems, el arrendamiento -por medio de tarifas de acceso y uso- ofrece a los clientes un medio para participar en sistemas de redes que los individuos y la mayora de las organizaciones no podran costear ni operar por s mismos (Lovelock,2015).

            img.mt-5.img-a(src="@/assets/curso/img_15.png").img-fluid



        .sub-ecored.mt-5#tema1_2_2
            h3 1.2.2. Caractersticas

        p Segn Kotler (2017) se tienen en cuenta cuatro caractersticas especiales de los servicios al disear programas de marketing: intangibilidad, inseparabilidad, variabilidad y caducidad.

        AcordionA.mb-5(tipo="b" clase-tarjeta="tarjeta tarjeta--azul")
            .row(titulo="Intangibilidad")
                .col-md-10.mb-4.mb-md-0
                p Los servicios no se pueden observar, degustar, tocar, escuchar ni oler antes de adquirirlos. Por ejemplo, la gente que se somete a una ciruga cosmtica no puede ver los resultados antes de la compra. Para reducir la incertidumbre, los consumidores buscan seales acerca de la calidad de un servicio. (Kotler,2017) 
            div(titulo="Inseparabilidad")
                .col-md-10.mb-4.mb-md-0
                p Los productos tangibles se producen, se almacenan, se venden y finalmente se consumen. En cambio, los servicios primero se venden, y se producen y consumen al mismo tiempo. La inseparabilidad del servicio se refiere a que ste no puede separarse de sus proveedores, los cuales son tanto individuos como mquinas. Tanto el proveedor como el cliente influyen en el resultado del servicio. (Kotler,2017)
            div(titulo="Variabilidad")
                .col-md-10.mb-4.mb-md-0
                p Implica que la calidad de los servicios depende de quin los proporciona, as como de cundo, dnde y cmo los proporciona. Ej. En un hotel tienen la reputacin de dar mejor servicio, quizs el recepcionista sea alegre y eficiente mientras que el botones sea desagradable y lento. (Kotler,2017)
            div(titulo="Caducidad")
                .col-md-10.mb-4.mb-md-0
                p Los servicios no pueden almacenarse para su venta o uso posterior. (Kotler,2017)


        img.mt-5.img-a(src="@/assets/curso/img_16.png").img-fluid

        .row.mt-5
                .col-1.col-md-1.flecha
                    img(src="@/assets/curso/img_6.png").img-icon
                .col-9.col-md-9
                    p #[strong Evaluacin de la calidad del servicio.]

        p Una vez que un consumidor prueba un servicio, cmo lo evala? Principalmente, al comparar las expectativas sobre una oferta de servicio con la experiencia real que tiene con el mismo. Las diferencias entre las expectativas y la experiencia del consumidor se evalan con base en dimensiones de la calidad del servicio. Las comunicaciones verbales, las necesidades personales, las experiencias previas y las actividades promocionales influyen en las expectativas, mientras que las experiencias reales estn determinadas por la manera en la que una organizacin entrega su servicio en particular a travs de competencias interpersonales (Kerin,2018).

        img.mt-5.img-a(src="@/assets/curso/img_17.png").img-fluid

        .sub-ecored.mt-5#tema1_3
            h2 1.3. Marca

        img.mt-5.img-a(src="@/assets/curso/img_18.png").img-fluid

        p.mt-5 La marca de un producto es aquello que lo identifica, que lo diferencia de otros similares, de manera que se convierte en el trasmisor de la esencia y los beneficios que aporta el producto, as como la proyeccin en el mercado de la imagen y el prestigio de la empresa que lo fabrica o lo comercializa (Martnez,2014).

        p Para Hernndez (2017) Los objetivos que se persiguen para otorgar una marca a un producto son: ser un signo de garanta para el producto, dar prestigio y seriedad a la empresa fabricante y ayudar a que se venda mejor el producto y las caractersticas primordiales de las marcas ponen nfasis en que su nombre no debe ser genrico; ste describe realmente al producto. El nombre debe ser corto, debe ser fcil de recordar, el sentido del nombre debe ser agradable, debe ser adaptable a cualquier medio de publicidad y se debe identificar fcilmente a simple vista.

        p Ricardo Hoyos (2016) define la importancia que tienen las marcas para el ser humano contemporneo, como individuo y como ser social. A travs de la marca, el ser humano se legitima frente a s mismo y frente a los dems; por medio de ella se establecen afinidades con sus grupos de inters y distancias con personas y grupos sociales que considera diferentes. En razn de la marca, los individuos crean identidad de s mismos y de sus grupos sociales.

        p En el siguiente cuadro se muestran los beneficios de la marca para el consumidor

