        p.text-center Fuente: Romn, 1988

        p La administracin de la marca es la actividad en la que una organizacin emplea un nombre, frase, diseo o smbolos, o combinaciones de estos, para identificar sus productos y distinguirlos de los competidores. Un nombre de marca es cualquier palabra, elemento (diseo, sonido, forma o color) o combinacin de estos, que se emplea para distinguir los bienes o servicios de un vendedor. El nombre comercial es una identidad mercantil y legal bajo la cual una empresa hace negocios. Una marca registrada identifica a una empresa registrada legalmente con su nombre de marca o con su nombre comercial, por lo que tiene la exclusividad de su uso, evitando que otros lo empleen. (Kerin,2018)

        .row.mt-5
                .col-1.col-md-1.flecha
