                .col-9.col-md-9
                    p #[strong Tipos de marcas.]

        img.mt-5.img-a(src="@/assets/curso/img_19.png").img-fluid

        .row.justify-content-center.mt-5.my-5
            .col-10.col-md-9

                p.header-step
                    span.header-step-number.text-color-orange 01
                    span.header-step-title
                        | #[strong Marca corporativa.] Se habla de marca corporativa cuando identifica a una empresa; puede, incluso, ser la misma de sus productos. Las empresas muy pequeas y con un portafolio poco diversificado o empresas de servicios, no asignan a cada producto una marca, sino que todo lo anuncian y comercializan con la marca de la organizacin, es decir, con la marca corporativa. Para el caso de empresas grandes y diversificadas, la marca corporativa regularmente es diferente a la de sus productos. Ej. Multinacionales extranjeras Unilever y Procter & Gamble y las multinacionales colombianas Nutresa y Quala. (Hoyos,2016

                p.header-step
                    span.header-step-number.text-color-orange 02
                    span.header-step-title
                        | #[strong Marca de producto.] La marca de producto es aquella que se usa para identificar un bien o un servicio en particular. Como ejemplo de estas marcas se pueden mencionar: Fruco, Coco, Axe, Lux, Rexona, Vasenol y Knorr, que pertenecen a Unilever, una de las empresas lderes en consumo masivo a nivel mundial. (Hoyos,2016)

                p.header-step
                    span.header-step-number.text-color-orange 03
                    span.header-step-title
                        | #[strong Marcas propias o marca de distribuidor.] Es aquella que fabrica el canal para vender en sus puntos de venta, compitiendo con las marcas de los fabricantes. Se clasifican en dos: marca blanca se venden con el nombre del detallista (marca xito o Ekono) y las privadas que se venden con un nombre diferente al nombre de la cadena, pero que son fabricadas por esta o fabricadas por un tercero para esta. Arkitect y Bronzini, en vestuario; Simply, en electrodomsticos y Taeq en alimentos, son ejemplos de marcas privadas de almacenes xito. (Hoyos,2016)

                p.header-step
                    span.header-step-number.text-color-orange 04
                    span.header-step-title
                        | #[strong Marca colectiva.] La desarrollan asociaciones de productores, fabricantes, prestadores de servicios, organizaciones o cualquier grupo de personas para identificar sus bienes y servicios. La marca colectiva busca explotar el buen nombre del cual gozan los integrantes y por extensin, la asociacin misma, generando una ventaja competitiva frente a otros fabricantes que trabajan de manera individual. Ejemplo la Federacin de Ganaderos Fedegan, present al mercado una marca colectiva para respaldar la produccin de carne de seis frigorficos asociados. El sello garantiza el origen del ganado, el sistema de clasificacin de canales, los mecanismos de refrigeracin y maduracin de las carnes (Hoyos,2016)


                p.header-step
                    span.header-step-number.text-color-orange 05
                    span.header-step-title
                        | #[strong Denominacin de origen.] Es el nombre o indicacin de un lugar geogrfico, que puede ser un pas, una regin determinada o una ciudad, que se asocia a un producto con el fin de darle ms valor frente a los consumidores, ya que garantiza que dicho producto sea originario de un lugar y, por las costumbres de produccin o transformacin de sus habitantes, tiene unas caractersticas y una reputacin que lo hacen diferente de los productos semejantes provenientes de otros lugares geogrcos (SIC, 2012) En Colombia se puede mencionar las artesanas de Rquira, el bizcocho de Achira de Huila y el emblemtico Caf de Colombia. (Hoyos,2016)

                p.header-step
                    span.header-step-number.text-color-orange 06
                    span.header-step-title
                        | #[strong Marca de certificacin.] Son las que se utilizan para garantizar una calidad o estndar de un producto o servicio (SIC, 2012). Estn las marcas ingrediente, los ecosellos, y las marcas que certifican que las empresas o productos cumplen con ciertas normas de calidad. Ejemplos Cotton USA. (Hoyos,2016)

                p.header-step
                    span.header-step-number.text-color-orange 07
                    span.header-step-title
                        | #[strong Marcas destino.] Son las desarrolladas para pases (marca pas), para ciudades o zonas de las ciudades. Hoy en da es comn hablar de city marketing, haciendo referencia al marketing que se hace para un destino turstico y de inversin. La marca pas se usa para hacer promocin hacia el exterior, buscando bsicamente la promocin de las exportaciones y del turismo, adems la promocin de la inversin extranjera. (Hoyos,2016)

                p.header-step
                    span.header-step-number.text-color-orange 08
                    span.header-step-title
                        | #[strong Marcas personales.] Aunque desde lo legal no existe este tipo de marca, el nombre de las personas, que existe en el concepto del registro civil y no del marcario, es una marca y, desde el marketing, debe recibir el tratamiento que se da a una marca comercial. Esto es importante para quienes viven del prestigio de sus nombres, como los profesionales independientes, los polticos y los artistas e, inclusive, quienes son empleados. Personal branding Ej. Natalia Paris, quien comercializa, entre otros productos, ropa interior, calzado, aceites, perfumes, revistas y servicios de telecomunicaciones. 

