        p.mt-5 Kotler (2017) determina que el branding representa una decisin difcil y las principales decisiones de estrategia de marca implican la seleccin del nombre de marca, co-branding y el desarrollo de marca.

        .row.justify-content-center.mt-5.my-5
            .col-10.col-md-9
                ul.list-unstyled
                    li.justify-content-center
                        i.fa.fa-angle-right.fa-lg.mr-3.text-color-orange
                        | #[strong Seleccin del nombre de marca.] Un buen nombre contribuye al xito de un producto; sin embargo, encontrar el mejor nombre de marca es una tarea difcil. Inicia con una revisin cuidadosa del producto y de sus beneficios, del mercado meta y de las estrategias de marketing propuestas. Despus de eso, la asignacin del nombre se vuelve una combinacin de ciencia, arte y un poco de instinto. Algunas cualidades deseables para un nombre de marca son:

