        .row.justify-content-center.mt-5.my-5
            .col-10.col-md-9
                ul.list-unstyled
                    li.justify-content-center
                        i.fa.fa-angle-right.fa-lg.mr-3.text-color-orange
                        | #[strong Co-branding.] Si dos nombres de marca establecidos por diferentes compaas se usan en el mismo producto, surge el co-branding. Cada marca opera una categora diferente, las marcas combinadas crean mayor atractivo para el consumidor y aumentan el capital de marca. Ej. Taco Bell y Doritos formaron equipo para crear los Doritos Locos Tacos. 

                    li.mt-5.justify-content-center
                        i.fa.fa-angle-right.fa-lg.mr-3.text-color-orange
                        | #[strong Desarrollo de marca.] Una compaa tiene cuatro opciones para desarrollar una marca: 

