                        p.mt-3.py-3 Extensiones de marca. Una extensin de marca implica el uso de un nombre de marca exitoso para lanzar productos nuevos o modificados en una categora nueva. 

            .col-12.col-md-8
                .row.justify-content-center.align-items-center.mt-5.card-text
