                        p.mt-3.py-3 Marcas nuevas. Una empresa podra considerar que el poder de su nombre de marca existente est disminuyendo, y que necesita un nuevo nombre de marca. O quizs una empresa idee un nuevo nombre de marca cuando ingresa a una nueva categora de productos, donde ninguno de sus nombres de marca actuales es adecuado.

