        img.mt-5.img-a(src="@/assets/curso/img_22.png").img-fluid

        p.mt-5 Las marcas exitosas y establecidas asumen una personalidad de marca, que es un conjunto de caractersticas humanas asociadas con un nombre de marca. Los estudios demuestran que los consumidores suelen asignar rasgos de personalidad a los productos: tradicionales, romnticos, resistentes, sofisticados, rebeldes, y que eligen las marcas que concuerdan ms con la imagen propia o deseada. Se da personalidad a una marca mediante la publicidad que representa a cierto usuario o situacin de uso y que transmite ciertas emociones o sentimientos que se asociaran con la marca. E. Harley-Davidson tiene rasgos asociados con masculinidad, desafo e individualismo vigoroso (Kerin,2018).


        .sub-ecored.mt-5#tema1_3_1
            h3 1.3.1. Branding

        img.mt-5.img-a(src="@/assets/curso/img_23.png").img-fluid

        p.mt-5 El branding o la creacin de una marca es un proceso integrativo, que busca construir marcas poderosas; es decir, marcas ampliamente conocidas, asociadas a elementos positivos, deseadas y compradas por una base amplia de consumidores. Esto incluye la definicin de la identidad o imagen que se quiere tener en un mercado, su diseo grfico y la puesta en escena de la marca frente a determinados pblicos, de manera que tome relevancia y, adems, rentabilidad a travs de todos los procesos de compra y recompra de la misma (Hoyos,2016).

        .row.justify-content-center.align-items-center.mt-5
            .col-auto
                img(src="@/assets/curso/img_24.png", style="max-width: 120px").img-fluid.mb-3.mb-md-0
            .col-10
                hr.border-color-primary.border-2
                p #[strong Capital de marca.]
                p El capital de marca es el efecto diferencial que el conocimiento del nombre de una marca tiene sobre la respuesta del cliente ante el producto o su comercializacin. Es una medida de la capacidad que tiene la marca para obtener la preferencia y lealtad del cliente. Una marca tiene un capital positivo cuando los consumidores reaccionan de manera ms favorable ante ella que ante una versin genrica o sin marca del mismo producto. A la inversa, una marca tiene un capital negativo si los consumidores reaccionan ante ella, de manera menos favorable que a una versin sin marca (Kotler,2017).
                hr.border-color-primary.border-2
            .col-auto
                img(src="@/assets/curso/img_25.png", style="max-width: 120px").img-fluid.mb-3.mb-md-0
            .col-10
                p #[strong Top of mind.]
                p El top of mind o primera mencin, se da cuando una persona, al ser indagada por una categora especfica, nombra una marca primero dentro de una serie de marcas que menciona, es decir, que dicha marca tiene el top of mind para dicha persona. En una investigacin de top of mind, se preguntar a una base de personas, de un grupo objetivo especfico, por marcas que recuerde y se estimar el promedio de personas que recuerdan una marca por sobre las dems. Dicho porcentaje ser el top of mind de la categora. En Colombia, anualmente, la Revista Dinero y la empresa de investigacin de mercados, Invamer Gallup, realizan una investigacin para establecer el top of mind de las marcas que compiten en este mercado. (Hoyos,2016) 
                hr.border-color-primary.border-2
            .col-auto
                img(src="@/assets/curso/img_26.png", style="max-width: 120px").img-fluid.mb-3.mb-md-0
            .col-10
                p #[strong E-branding.]
                p Rowles (2014) define al e-branding como la personalidad de la organizacin, servicio o producto creado por la suma de todas experiencias que una persona tiene con la marca, incluyendo la interaccin con las redes sociales y las evaluaciones crticas online del desempeo de una marca (online reviews). Es decir, en esencia la marca y sus indicadores como conocimiento de marca, posicionamiento, preferencia de marca, intencin de compra, re-compra y recomendacin de marca, permanecen intactas. Lo que cambia con el advenimiento de la era digital es la forma en que se construye marca. 
                hr.border-color-primary.border-2

        .sub-ecored.mt-5#tema1_3_2
            h3 1.3.2. Posicionamiento
        
        img.mt-5.img-a(src="@/assets/curso/img_27.png").img-fluid

        p.mt-5 El posicionamiento es lo que queremos que el consumidor piense acerca de nuestro producto, es decir, aquello que se constituye en la idea bsica de venta, que generalmente es una afirmacin o una frase. El posicionamiento se requiere cuando hay muchas alternativas de productos y marcas, muchos anuncios publicitarios, as como cuando la competencia ataca y cuando el producto entra al mercado. (Prieto,2013)

        p Para Prieto (2013) al hablar de posicionamiento se deben plantear las siguientes inquietudes: 

