                        li.justify-content-center
                            i.fa.fa-angle-right.fa-lg.mr-3.text-color-orange
                            | Es efectiva la publicidad aplicada al producto?

        p.mt-5 Segn Kotler (2017), el objetivo es posicionar las marcas con claridad en la mente del mercado meta. #[br]Las marcas se pueden posicionar en tres niveles.

        .row.justify-content-center.mt-5.my-5
            .col-10.col-md-9
                ul.list-unstyled
                    li.justify-content-center.mt-5
                        i.fa.fa-angle-right.fa-lg.mr-3.text-color-orange
                        | #[strong Atributos del producto.] Por ejemplo, P&G con su marca Pampers se enfoc en atributos tales como la absorcin de fluidos, el ajuste y la facilidad de desecho.

                    li.justify-content-center.mt-5
                        i.fa.fa-angle-right.fa-lg.mr-3.text-color-orange
                        | #[strong Beneficio deseable.] Ej. Pampers puede ir ms all al hablar de los beneficios de la absorcin para la salud de la piel debido a que sta se conserva seca. 

                    li.justify-content-center.mt-5
                        i.fa.fa-angle-right.fa-lg.mr-3.text-color-orange
                        | #[strong Creencias y valores slidos.] Atraccin de clientes en un nivel profundo y emocional. Ej. para los padres de familia, Pampers significa mucho ms que capacidad de absorcin para mantener seca la piel del beb. El sitio web de Pampers (www.pampers.com) posiciona a Pampers como una marca de amor, sueo y juego que se preocupa por que los bebs estn felices, por las relaciones entre padres e hijos y por el cuidado del beb. 

                    li.justify-content-center.mt-5
                        i.fa.fa-angle-right.fa-lg.mr-3.text-color-orange
                        | #[strong Lovemarks (marcas amadas).] Es decir, marcas que la gente ama. A los clientes no solamente les gustan esas marcas, sino que tienen fuertes conexiones emocionales con ellas y las aman incondicionalmente. 

        p Para Ricardo Hoyos (2016) las principales variables para posicionar un producto son:

        .row.justify-content-center.align-items-center.mt-5 

            .col-12.col-md-5
