                    li.justify-content-center.mt-5
                        i.fa.fa-angle-right.fa-lg.mr-3.text-color-orange
                        | #[strong Conservacin:] impide que el producto que contiene se deteriore antes de ser utilizado, un elemento voltil como el alcohol si no se envasa debidamente se evapora. (Martnez,2014)

                    li.justify-content-center.mt-5
                        i.fa.fa-angle-right.fa-lg.mr-3.text-color-orange
                        | #[strong Proteccin:] La proteccin fsica se requiere para defender al producto contra los choques o impactos, cadas, vibraciones, para evitar que se rompa o se desajuste y para impedir posibles mermas o fugas. La proteccin qumica se requiere para defender al producto contra los efectos del oxgeno y otros elementos (como el calor, el fro, la humedad o los rayos ultravioleta) que favorecen la aparicin y el desarrollo de hongos que degradan la apariencia sensorial y nutricional. (Hernandez,2017)

                    li.justify-content-center.mt-5
                        i.fa.fa-angle-right.fa-lg.mr-3.text-color-orange
                        | #[strong Seguridad:] se evitan posibles accidentes que algunos productos, por sus caractersticas podran provocar como una bombona de gas o una botella de agua fuerte. (Martnez,2014)

                    li.justify-content-center.mt-5
                        i.fa.fa-angle-right.fa-lg.mr-3.text-color-orange
