        .sub-ecored.mt-5#tema1_6
            h2 1.6. Embalaje.

        .row.justify-content-center.align-items-center.mt-5
            .col
                p Hernndez (2017) Define el embalaje como un contenedor colectivo, que agrupa varios envases primarios y secundarios (empaques), con la finalidad de unificarlos, protegerlos, facilitar su manejo, almacenamiento, transporte y distribucin. Tambin se le conoce como el envase de distribucin. Las dos principales funciones del embalaje son unificar y proteger la mercanca durante su distribucin.
            .col-auto
                img(src="@/assets/curso/img_34.png", alt="").img-icon

        p.header-step
                    span.header-step-number.text-color-orange a
                    span.header-step-title
                        | #[strong Funcin de unificacin.] Los productos de consumo no se pueden comercializar a granel en los sistemas de distribucin masiva. No es posible distribuir ningn producto si no est debidamente embalado. Las cargas formadas por productos embalados colectivamente llegan a ser homogneas o heterogneas. Cuando la carga es homognea no es difcil resolver su acomodo, pero cuando es heterognea (con pesos, dimensiones y materiales distintos), surge el problema de su acomodo, por la posibilidad de cadas y daos.

        p.header-step
            span.header-step-number.text-color-orange b
            span.header-step-title
                | #[strong Funcin de proteccin.] La proteccin que da el embalaje al producto es de vital importancia para llevar a efecto su distribucin masiva. Los materiales del envase y el embalaje que se utilizan comnmente son:

        .row.justify-content-center
            .col-10.col-md-9
                ul.list-unstyled
                    li.justify-content-center.mt-5
                        i.fa.fa-angle-right.fa-lg.mr-3.text-color-orange
                        | #[strong Papel.] Es uno de los materiales ms utilizados para el envase y puede combinarse con el aluminio y las pelculas plsticas. Su funcin principal es evitar fricciones entre las piezas, as como proporcionar una envoltura desechable que evite las raspaduras y la vibracin. El papel no resiste la humedad ni las grasas.


                    li.justify-content-center.mt-5
                        i.fa.fa-angle-right.fa-lg.mr-3.text-color-orange
                        | #[strong Cartn.] Se utiliza ms como amortiguador que como envase. Como embalaje es posible darle propiedades impermeables aplicando pelculas de parafina o polietileno.

                    li.justify-content-center.mt-5
                        i.fa.fa-angle-right.fa-lg.mr-3.text-color-orange
                        | #[strong Madera.] Se emplea para la fabricacin de tarimas, cajas, rejas o jaulas y carretes. Usualmente se utiliza la madera de pino por resistente y barata.

                    li.justify-content-center.mt-5
                        i.fa.fa-angle-right.fa-lg.mr-3.text-color-orange
                        | #[strong Plsticos.] Sirve para fabricar envases y embalajes de alta productividad; sin embargo, el plstico no es biodegradable por lo que representa problemas ecolgicos graves a futuro. Este tipo de envases se deben reciclar.

                    li.justify-content-center.mt-5
                        i.fa.fa-angle-right.fa-lg.mr-3.text-color-orange
                        | #[strong Vidrio.] Es el material tradicional para el envase de alimentos, vinos, algunos frmacos y cosmticos. Algunas de sus caractersticas son su estabilidad qumica, que son 100% impermeables, la esterilizacin, refractabilidad, retornabilidad y la presentacin de calidad.

                    li.justify-content-center.mt-5
                        i.fa.fa-angle-right.fa-lg.mr-3.text-color-orange
                        | #[strong Metales.] La hoja de lata es mayormente utilizada para la conservacin de alimentos, as como para el envasado de pinturas, solventes y productos qumicos, mientras que el aluminio lo es para los refrescos.

