            h2 1.9. Marketing Social

        img.img-a(src="@/assets/curso/img_48.png").img-fluid 

        p.mt-5 El marketing social se aplica cuando existen organizaciones que, comprometidas con la situacin de una ciudad, desarrollan programas de comunicacin, buscando concientizar a los ciudadanos acerca de la necesidad de mantener en buen estado los parques, no fumar en lugares pblicos, respetar a los ancianos, navidad sin plvora o sin nios quemados, entre otras. En general el marketing social pretende generar, a travs de sus acciones, proteccin y bienestar para la comunidad (Prettel,2016). 

        p Lamb, Hair y McDaniel (2011), plantean en este sentido: #[strong La responsabilidad social corporativa] (de una empresa), como la que se refiere al inters de la misma por la forma en que su decisin afecta a la sociedad. Si bien la compaa tiene que considerar sus necesidades econmicas, debe operar tambin dentro de la ley, hacer lo que es tico y justo, y ser una buena ciudadana.

        p Para Forero (2017), el marketing social hace referencia a las consideraciones relacionadas con la responsabilidad social, el medio ambiente y el respeto de los valores de las personas y de las comunidades. En esta nueva concepcin, el marketing ha pasado de ser til para la promocin de productos para convertirse en un instrumento vital para promover inclusive ideas y servicios (Nieto 1988).

        p Segn Stanton, (2007) la responsabilidad social de una empresa se extiende las dimensiones de amplitud y compromiso de sus metas de marketing lo suficiente para cumplir su responsabilidad social, pone en prctica lo que se conoce como el concepto de marketing social.

        .row.justify-content-center.mt-5.my-5
                .col-10.col-md-9
                    ul.list-unstyled
                        li.justify-content-center.mt-5
                            i.fa.fa-angle-right.fa-lg.mr-3.text-color-orange
                            | Amplitud del marketing, una empresa reconoce que su mercado abarca no slo a los compradores de sus productos, sino tambin a cualquier afectado directamente por sus operaciones. Ejemplo los detallistas de la moda internacional con la disponibilidad de empleos en economas del tercer mundo. 

                        li.justify-content-center.mt-5
                            i.fa.fa-angle-right.fa-lg.mr-3.text-color-orange
                            | Extender la dimensin del compromiso de sus metas de marketing, significa que una empresa debe reconocer que atender las necesidades ms amplias de la sociedad para satisfacer las necesidades sociales de sus clientes al igual que las econmicas.

        p El marketing social consiste en motivar comportamientos que generen bienestar individual y social. Los programas de marketing social abarcan un amplio rango de asuntos. Se desarrollan campaas de propaganda social que hablan de asuntos como salud preventiva, educacin, sustentabilidad ambiental, derechos humanos y seguridad personal. En todo caso, el marketing social implica mucho ms que difundir campaas de informacin; implica una amplia variedad de estrategias y herramientas de mezcla de marketing diseadas para motivar cambios sociales benficos (Kotler,2017).

