        p Para la proteccin de los derechos de autor esta la direccin nacional de derechos de autor http://derechodeautor.gov.co/ Ente institucional para fortalecer la debida y adecuada proteccin de los diversos titulares del derecho de autor y los derechos conexos, contribuyendo a la formacin, desarrollo y sustentacin de una cultura nacional de respeto por los derechos de los diversos autores y titulares de las obras literarias y artsticas.

